  #contact{
    height: 80vh;
  }

  .contact-form-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }

  .heading{
    padding-bottom: 40px;
  }
  .contact-info {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 20px;
  }

  .contact-info div {
    text-align: center;
  }

  .contact-info p {
    margin-top: 10px;
  }

  .contact-form {
    display: flex;
    flex-direction: column;
    width: 70%;
  }

  .contact-form input,
  .contact-form textarea {
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid green;
    border-radius: 10px;
    font-size: 16px;
  }

  .contact-form textarea {
    height: 100px;
    resize: none;
  }

  .submit-button {
    padding: 10px;
    background-color: green;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.4s ease-in-out;
  }

  .submit-button:hover {
    background-color: rgb(1, 153, 1);
  }